<template>
  <div class="board-container">
    <!-- 상단 버튼 및 검색 -->
    <div class="header-container" v-if="currentPage === 'list'">
      <div class="header-buttons">
        <button class="write-button" @click="setPage('write')">글작성</button>
        <button class="home-button" @click="goHome">처음 페이지</button>
      </div>

      <div class="search-container">
        <input
          type="text"
          v-model="searchQuery"
          class="search-input"
          placeholder="작성자 또는 제목 검색"
        />
        <button class="search-button" @click="searchPosts">검색</button>
      </div>
    </div>

    <!-- 리스트 화면 -->
    <div v-if="currentPage === 'list'">
      <table class="board-table">
        <thead>
          <tr>
            <th>번호</th>
            <th>작성자</th>
            <th>제목</th>
            <th>작성일</th>
            <th>수정일</th>
            <th>비밀번호</th>
            <th>보기</th>
            <th>수정</th>
            <th>삭제</th>
            <th>답변</th>
            <th>답변 확인</th>
          </tr>
        </thead>
        <tbody>
          <tr  class="board_imfo" v-for="(item, index) in paginatedAndFilteredList" :key="index">
            <td>{{ getRowNumber(index) }}</td>
            <td>{{ getMaskedWriter(item.writer) }}</td>
            <td>{{ item.title }}</td>
            <td>{{ item.date }}</td>
            <td>{{ item.modifiedDate || '-' }}</td>
            <td>
              <input
                type="password"
                v-model="item.inputPassword"
                class="password-input"
                placeholder="비밀번호"
              />
            </td>
            <td>
              <button
                class="view-button"
                :disabled="!isPasswordValid(item)"
                @click="viewPost(index + (currentPageNumber - 1) * itemsPerPage)"
              >
                보기
              </button>
            </td>
            <td>
              <button
                class="edit-button"
                :disabled="!isPasswordValid(item)"
                @click="editPost(index + (currentPageNumber - 1) * itemsPerPage)"
              >
                수정
              </button>
            </td>
            <td>
              <button
                class="delete-button"
                :disabled="!isPasswordValid(item)"
                @click="deletePost(index + (currentPageNumber - 1) * itemsPerPage)"
              >
                삭제
              </button>
            </td>
            <td>
                <button
                  class="answer-button"
                  :style="{ backgroundColor: item.answer ? '#f0ad4e' : '#f8f8f8' }"
                  :disabled="item.inputPassword !== adminPassword"
                  @click="answerPost(index + (currentPageNumber - 1) * itemsPerPage)"
                >
                  답변
                </button>
              </td>
            <td>
              <button
                class="view-button"
                :style="{ backgroundColor: item.answer ? '#7eb2ee' : '#f8f8f8' }"
                :disabled="!isPasswordValid(item)"
                @click="viewPost(index + (currentPageNumber - 1) * itemsPerPage)"
                v-if="item.answer"
              >
                답변 확인
              </button>
            </td>
          </tr>
        </tbody>
      </table>

      <div class="pagination">
        <button class="prev-button" @click="prevPage">&lt;</button>
        <span>{{ currentPageNumber }} / {{ totalPages }}</span>
        <button class="next-button" @click="nextPage">&gt;</button>
      </div>
    </div>

    <!-- 작성, 수정, 답변 공통 폼 -->

    <div class="formstlye" v-if="['write', 'edit'].includes(currentPage)">
      <div class="form-container">
        <div class="form-group">
          <label class="label-writer">작성자</label>
          <input type="text" v-model="formData.writer" />
        </div>
        <div class="form-group">
          <label class="label-title">제목</label>
          <input type="text" v-model="formData.title" />
        </div>
        <div class="form-group">
          <label class="label-password">비밀번호</label>
          <input
            type="password"
            v-model="formData.password"
            placeholder="필수 입니다."
          />
        </div>
        <div class="w-date">
          <label class="label-date">작성일</label>
          <input type="text" v-model="formData.date" disabled />
        </div>
        <div class="w-date">
          <label class="label-modified-date">수정일</label>
          <input type="text" v-model="formData.modifiedDate" disabled />
        </div>
        <div v-if="currentPage !== 'answer'" class="form-group">
          <label class="label-content">내용</label>
          <textarea v-model="formData.content" class="large-textarea"></textarea>
        </div>
      
        <div class="form-buttons">
          <button class="cancel-button yellow smaller" @click="goHome">취소</button>
          <button
            class="submit-button red smaller"
            @click="submitPost"
          >
            {{ currentPage === 'write' ? '제출' : '완료' }}
          </button>
        </div>
      </div>
    </div>


    <!-- 보기 화면 -->
    <div v-if="currentPage === 'view'">
      <div class="view-container">
        <h2>{{ formData.title }}</h2>
        <p><strong>작성자</strong> {{ formData.writer }}</p>
        <p><strong>작성일</strong><input type="text" v-model="formData.date" disabled /></p>
        <p><strong>수정일</strong> <input type="text" v-model="formData.modifiedDate" disabled /></p>
        <p><strong>내용</strong></p>
        <textarea v-model="formData.content" class="large-textarea" disabled></textarea>

        <!-- 답변 섹션 -->
        <div class="answers-section" v-if="formData.answer">
          <h3>답변</h3>
          <textarea
            v-model="formData.answer"
            class="large-textarea"
            disabled
          ></textarea>
        </div>

        <button class="home-button" @click="goHome">목록으로</button>
      </div>
    </div>

    <!-- 답변 작성 화면 -->
    <div v-if="currentPage === 'answer'">
      <div class="form-container">
        <h3>답변 작성/수정</h3>
        <div class="form-group">
          <label class="label-writer">작성자</label>
          <input type="text" v-model="formData.writer" disabled />
        </div>
        <div class="form-group">
          <label class="label-title">제목</label>
          <input type="text" v-model="formData.title" disabled />
        </div>
        <div class="w-date">
          <label class="label-date">작성일</label>
          <input type="text" v-model="formData.date" disabled />
        </div>
        <div class="w-date">
          <label class="label-modified-date">수정일</label>
          <input type="text" v-model="formData.modifiedDate" disabled />
        </div>
        <div class="form-group">
          <label class="label-content">내용</label>
          <textarea v-model="formData.content" class="large-textarea" disabled></textarea>
        </div>
        <div class="form-group">
          <label class="label-answer">답변</label>
          <textarea
            v-model="formData.answer"
            class="large-textarea"
            placeholder="답변 내용을 작성하거나 수정하세요."
          ></textarea>
        </div>
        <div class="form-buttons">
          <button class="cancel-button yellow smaller" @click="goHome">취소</button>
          <button class="submit-button red smaller" @click="submitPost">완료</button>
        </div>
      </div>
    </div>

    <!-- 비밀번호 입력 경고 모달 -->
    <div v-if="showPasswordWarning" class="modal-overlay">
      <div class="modal-content">
        <p>비밀번호가 틀렸습니다. 다시 확인해주세요!</p>
        <button class="modal-button" @click="closePasswordWarning">확인</button>
      </div>
    </div>

    <!-- 글 작성 시 비밀번호 입력 안내 모달 -->
    <div v-if="showPasswordRequiredModal" class="modal-overlay">
      <div class="modal-content">
        <p>비밀번호를 입력해주세요!</p>
        <button class="modal-button" @click="closePasswordRequiredModal">확인</button>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  data() {
    return {
      currentPage: 'list',
      currentPageNumber: 1,
      itemsPerPage: 10,
      adminPassword: '110110',
      searchQuery: '',
      boardList: [],
      formData: {
        writer: '',
        title: '',
        content: '',
        password: '',
        inputPassword: '',
        answer: '',
        date: '',
        modifiedDate: '',
      },
      editIndex: null,
      showPasswordWarning: false,
      showPasswordRequiredModal: false,
    };
  },
  computed: {
    paginatedAndFilteredList() {
    const filteredList = this.searchQuery
      ? this.boardList.filter(
          (item) =>
            item.writer.includes(this.searchQuery) ||
            item.title.includes(this.searchQuery)
        )
      : this.boardList;

    const start = (this.currentPageNumber - 1) * this.itemsPerPage;
    const end = start + this.itemsPerPage;
    
    // 역순으로 출력할 리스트를 가져옵니다.
    return filteredList.slice().reverse().slice(start, end);
  },
  totalPages() {
    return Math.ceil(this.boardList.length / this.itemsPerPage);
  },
  },
  methods: {
    isPasswordValid(item) {
      return item.inputPassword === item.password || item.inputPassword === this.adminPassword;
    },
    setPage(page) {
      this.resetFormData();
      this.currentPage = page;
    },
    editPost(index) {
      const reversedIndex = this.boardList.length - (this.currentPageNumber - 1) * this.itemsPerPage - index - 1;
      const item = this.boardList[index];
      if (this.isPasswordValid(item)) {
        this.formData = { ...item };
        this.editIndex = reversedIndex;
        this.currentPage = 'edit';
      }
    },
    answerPost(index) {
      const item = this.boardList[index];

      // 비밀번호 검증
      if (this.isPasswordValid(item)) {
        // item.answer 값이 없으면 기본값 설정
        if (item.answer === undefined || item.answer === null) {
          this.$set(this.boardList, index, { ...item, answer: true });  // '답변' 상태 true로 설정
        } else {
          this.$set(this.boardList, index, { ...item, answer: !item.answer });  // '답변' 상태 토글
        }
        
        this.formData = { ...item, answer: item.answer || '' };
        this.editIndex = index;
        this.currentPage = 'answer';
      }
    },

    // 작성자 writer 글자 수대로 *표시
    getMaskedWriter(writer) {
      if (!writer) return '';
  const visibleLength = 2; // 보일 글자 수 (2글자)
  const firstPart = writer.slice(0, visibleLength); // 처음 2글자
  const stars = '*'.repeat(writer.length - visibleLength); // 나머지 글자는 *로 마스킹
  return firstPart + stars;
  },


    submitPost() {
      if (this.currentPage === 'write' && !this.formData.password.trim()) {
        this.showPasswordRequiredModal = true; // 새 모달 표시
        return;
      }

      const currentDate = new Date().toLocaleString('ko-KR', { timeZone: 'Asia/Seoul' });

      if (this.currentPage === 'answer' && this.editIndex !== null) {
        this.boardList[this.editIndex].answer = this.formData.answer;
        this.editIndex = null;
        this.resetFormData();
        this.goHome();
        return;
      }

      if (this.editIndex !== null) {
        this.boardList.splice(this.editIndex, 1, {
          ...this.formData,
          modifiedDate: currentDate,
        });
      } else {
        this.boardList.push({
          ...this.formData,
          date: currentDate,
          modifiedDate: '',
          inputPassword: '',
          answer: '',
        });
      }

      this.editIndex = null;
      this.resetFormData();
      this.goHome();
    },
    closePasswordWarning() {
      this.showPasswordWarning = false;
    },
    closePasswordRequiredModal() {
      this.showPasswordRequiredModal = false;
    },
    resetFormData() {
      this.formData = {
        writer: '',
        title: '',
        content: '',
        password: '',
        inputPassword: '',
        answer: '',
      };
      this.editIndex = null;
    },
    goHome() {
      this.currentPageNumber = 1;
      this.currentPage = 'list';
    },
      // 번호를 역순으로 계산
  getRowNumber(index) {
    return this.boardList.length - (this.currentPageNumber - 1) * this.itemsPerPage - index;
  },
    viewPost(index) {
      
      const item = this.boardList[index];
      if (this.isPasswordValid(item)) {
        this.formData = { ...item };
        this.currentPage = 'view';
      }
    },
    deletePost(index) {
      const reversedIndex = this.boardList.length - (this.currentPageNumber - 1) * this.itemsPerPage - index - 1;
      const item = this.boardList[index];
      if (this.isPasswordValid(item)) {
        this.boardList.splice(reversedIndex, 1);
      }
    },
    prevPage() {
      if (this.currentPageNumber > 1) this.currentPageNumber--;
    },
    nextPage() {
      if (this.currentPageNumber < this.totalPages) this.currentPageNumber++;
    },
    searchPosts() {
      if (!this.searchQuery.trim()) alert('검색어를 입력해주세요.');
    },
  },
};
</script>

